.popup__container {
    position: relative;
    max-width: 430px;
    color: black;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 900px) {
    .popup__container {
        max-width: 282px;
    }
}